"use client";
import { useEffect, useState } from "react";
import { FaEdit, FaTrash } from "react-icons/fa";

export default function AllTasksPage() {
  const [tasks, setTasks] = useState([]);
  const token = localStorage.getItem("token");

  useEffect(() => {
    const fetchTasks = async () => {
      try {
        const res = await fetch("http://localhost:5000/api/alltasks", {
          headers: { Authorization: `Bearer ${token}` },
        });
        const data = await res.json();
        setTasks(data);
      } catch {
        console.error("Cannot reach backend!");
      }
    };
    fetchTasks();
  }, []);

  const markDone = async (id) => {
    await fetch(`http://localhost:5000/api/alltasks/${id}`, {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ pending: false }),
    });
    setTasks(tasks.map(t => t._id === id ? { ...t, pending: false } : t));
    window.location.href = "/dashboard"; // Redirect after marking completed
  };

  const deleteTask = async (id) => {
    await fetch(`http://localhost:5000/api/alltasks/${id}`, {
      method: "DELETE",
      headers: { Authorization: `Bearer ${token}` },
    });
    setTasks(tasks.filter(t => t._id !== id));
    window.location.href = "/dashboard"; // Redirect after delete
  };

  return (
    <div className="min-h-screen bg-gradient-to-br from-purple-200 via-blue-200 to-green-200 p-6">
      {/* Header with App Icon + Name */}
      <div className="flex justify-between items-center mb-6">
        <div className="flex items-center gap-2">
          <img src="/favicon.ico" alt="App Icon" className="w-10 h-10" />
          <h1 className="text-2xl font-bold text-purple-700">Task Manager</h1>
        </div>

        {/* +Create button */}
        <button
          onClick={() => window.location.href = "/create"}
          className="bg-green-600 text-white px-4 py-2 rounded hover:bg-green-700 transition"
        >
          + Create
        </button>
      </div>

      {/* Task list */}
      <div className="space-y-4">
        {tasks.length === 0 ? (
          <p className="text-center text-gray-700">No tasks yet.</p>
        ) : (
          tasks.map(task => (
            <div
              key={task._id}
              className="bg-white p-4 rounded-xl shadow-md flex justify-between items-center"
            >
              <div className="flex flex-col gap-1">
                <h2 className="font-semibold text-lg">{task.title}</h2>
                <p className="text-gray-600">{task.description}</p>
                <p className={task.pending ? "text-red-500" : "text-green-600"}>
                  {task.pending ? "Pending" : "Completed"}
                </p>
              </div>

              <div className="flex gap-2">
                {task.pending && (
                  <button
                    onClick={() => markDone(task._id)}
                    className="bg-green-500 text-white px-3 py-1 rounded hover:bg-green-600 transition"
                  >
                    Done
                  </button>
                )}
                <button
                  onClick={() => window.prompt("Edit task", task.title)}
                  className="text-blue-600 hover:text-blue-800"
                >
                  <FaEdit />
                </button>
                <button
                  onClick={() => deleteTask(task._id)}
                  className="text-red-600 hover:text-red-800"
                >
                  <FaTrash />
                </button>
              </div>
            </div>
          ))
        )}
      </div>
    </div>
  );
}
